                            clearable
                            label="เพิ่มรูปภาพ"
                            multiple
                            dense
                            counter
                            ref="file"
                            @change="inputChanged"
                          >
                            <template v-slot:selection="{ text, index }">
                              <v-chip
                                small
                                text-color="white"
                                color="#295671"
                                close
                                @click:close="remove(index)"
                              >
