<script>
import axios from "axios";
import imgPreview from "../components/previewImg";

export default {
  components: {
    imgPreview,
  },
  data() {
    return {
      imagePreviewURL: "",
      isLoading: false,
      isFormValid: false,
      showContent: {
        seller: false,
        buyer: false,
        addProduct: false,
      },
      productForm: {
        idTypeOfArgi: "",
        idAgri: "",
        speciesArgi: "",
        detailOfProduct: "",
        sizeOfProduct: "",
        unit: "",
        priceBegin: "",
        idSellerMember: "",
        priceEnd: "",
        monthOfProduct: [],
        typeOfStand: "",
        madeByOrder: "",
        imgCurFiles: [],
        img: [],
      },
      typeOfArgi: [],
      speciesItem: [],
      argiTd: [],
      productByArgiType: [],
      typeOfStand: [],
      monthOfYear: [],
      unitMk: [],
      mini: false,
      drawer: true,
      userInfo: [],
      formStep: {
        step1: false,
        step2: false,
      },
      baseUrl: "https://farmtd.hrdi.or.th/farmdd/admin/api/",
    };
  },
  computed: {
    cardWidth() {
      return this.mini ? 56 : 256;
    },
  },
  methods: {
    findSpecies() {
      let agriId = new FormData();
      agriId.append("agriId", this.productForm.idAgri);
      axios
        .post(`${this.baseUrl}product/postSpeciesByArgi.php`, agriId)
        .then((res) => {
          if (res.data.species) {
            this.speciesItem = res.data.species;
            if (this.speciesItem.length === 0) {
              this.speciesItem[0] = {
                agriId: this.productForm.idAgri,
                speciesId: 0,
                speciesName: "ไม่มี",
              };
            }
            console.log("speciesItem", this.speciesItem);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    remove(index) {
      this.productForm.img.splice(index, 1);
      this.productForm.imgCurFiles.splice(index, 1);
    },
    inputChanged(e) {
      console.log("change", e);
      this.productForm.img = [
        ...this.productForm.imgCurFiles,
        ...this.productForm.img,
      ];
    },
    errMsg(msg) {
      this.$swal.fire({
        icon: "info",
        title: msg,
        showConfirmButton: true,
        allowOutsideClick: false,
      });
      this.remove(this.productForm.img.length - 1);
    },
    findProductName(type) {
      this.productByArgiType = [];
      this.productByArgiType = this.argiTd.filter((e) => {
        return e.TypeOfArgiIdTypeOfArgi === type;
      });
      console.log("productByArgiType", this.productByArgiType);
    },
    showAddProduct() {
      this.formStep = {
        step1: true,
        step2: false,
      };
      this.showContent = {
        seller: false,
        buyer: false,
        addProduct: true,
      };
    },
    formStep2() {
      this.productForm.idSellerMember = localStorage.idMember;
      console.log(this.productForm);
      if (this.formStep.step1) {
        (this.formStep.step1 = false), (this.formStep.step2 = true);
      }
    },
    submitForm() {
      if (this.productForm.img.length > 5) {
        this.errMsg("ไม่สามาถเพิ่มรูปภาพได้เกิน 5 รูป");
        return;
      }

      this.isLoading = true;
      this.isFormValid = true;

      let formData = new FormData();
      formData.append("idTypeOfArgi", this.productForm.idTypeOfArgi);
      formData.append("detailOfProduct", this.productForm.detailOfProduct);
      formData.append("idSellerMember", this.productForm.idSellerMember);
      formData.append("idAgri", this.productForm.idAgri);
      formData.append("madeByOrder", this.productForm.madeByOrder);
      formData.append("monthOfProduct", this.productForm.monthOfProduct);
      formData.append("priceBegin", this.productForm.priceBegin);
      formData.append("priceEnd", this.productForm.priceEnd);
      formData.append("sizeOfProduct", this.productForm.sizeOfProduct);
      formData.append("speciesArgi", this.productForm.speciesArgi);
      formData.append("typeOfStand", this.productForm.typeOfStand);
      formData.append("titleImg", this.productForm.img[0].name);
      formData.append("unit", this.productForm.unit);

      for (var i = 0; i < this.productForm.img.length; i++) {
        let file = this.productForm.img[i];
        formData.append("img[" + i + "]", file);
        console.log(file);
      }
      axios
        .post(`${this.baseUrl}product/postAddProduct.php`, formData, {
          maxContentLength: Infinity,
          maxBodyLength: Infinity,
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          console.log(res);
          if (res.data === "True") {
            this.isLoading = false;
            this.$swal
              .fire({
                icon: "success",
                title: "เพิ่มสินค้าสำเร็จ",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "Home" });
              });
          }
        })
        .catch((err) => {
          if (err) {
            this.$swal
              .fire({
                icon: "error",
                title: "เกิดข้อผิดพลาด",
                text: "ไม่สามารถเพิ่มสินค้าได้",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "Home" });
              });
          }
        });
    },
    getUserInfo() {
      let username = new FormData();
      username.append("username", localStorage.user);
      if (localStorage.typeOfMember === "Seller") {
        username.append("table", "SellerMember_MK");
        axios
          .post(`${this.baseUrl}member/postFindSellerInfo.php`, username)
          .then((res) => {
            if (res.data.sellerUser !== 0) {
              this.userInfo = res.data.sellerUser[0];
              this.userInfo.sellerImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/profile/${res.data.sellerUser[0].sellerImg}`;
              this.showContent.seller = true;
              console.log("userInfo", this.userInfo);
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        axios
          .post(`${this.baseUrl}member/postFindUserInfo.php`, username)
          .then((res) => {
            if (res.data.buyerUser !== 0) {
              this.userInfo = res.data.buyerUser[0];
              this.userInfo.buyerImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/profile/${res.data.buyerUser[0].buyerImg}`;
              this.showContent.buyer = true;
              console.log("userInfo", this.userInfo);
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    checkLogin() {
      if (
        localStorage.idMember === "" ||
        localStorage.idMember === null ||
        localStorage.idMember === undefined
      ) {
        this.$router.push({ name: "Login" });
      }
    },
    showUserProfile() {
      this.showContent.addProduct = false;
      if (localStorage.typeOfMember === "Seller") {
        this.showContent.seller = true;
      } else {
        this.showContent.buyer = true;
      }
    },
    getTypeOfArgi() {
      axios
        .get(`${this.baseUrl}product/getTypeArgi.php`)
        .then((response) => {
          if (response.data.typeOfArgi !== 0) {
            this.typeOfArgi = response.data.typeOfArgi;
            this.typeOfArgi = this.typeOfArgi.filter((e) => {
              return e.nameTypeOfArgiMk !== null;
            });
            console.log("typeOfArgi", this.typeOfArgi);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getArgiTd() {
      axios
        .get(`${this.baseUrl}product/getArgiTd.php`)
        .then((res) => {
          this.argiTd = res.data.argiTd;
          console.log("getArgiTd", this.argiTd);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getTypeOFStand() {
      axios
        .get(`${this.baseUrl}product/getTypeOfStand.php`)
        .then((res) => {
          this.typeOfStand = res.data.typeOfStand;
          console.log("gettypeOfStand", this.typeOfStand);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getMonthOfYear() {
      axios
        .get(`${this.baseUrl}product/getMonthOfYear.php`)
        .then((res) => {
          this.monthOfYear = res.data.monthOfYear;
          console.log("getMonthOfYear", this.monthOfYear);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getUnitMk() {
      axios
        .get(`${this.baseUrl}product/getUnitMk.php`)
        .then((res) => {
          this.unitMk = res.data.unitMk;
          console.log("unitMk", this.unitMk);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.checkLogin();
    this.getUserInfo();
    this.getTypeOfArgi();
    this.getArgiTd();
    this.getTypeOFStand();
    this.getMonthOfYear();
    this.getUnitMk();
  },
  created() {},
};
</script>


